.doubt {
  @include container-adaptive();
  @include horizontal-paddings(0);
  @include vertical-paddings(30px);
  margin-bottom: 16px;
}

.doubt__title {
  width: 230px;
  margin: 0 auto;
  padding-bottom: 28px;
  text-align: center;
  font-size: 18px;
}

.doubt__text {
  width: 250px;
  margin: 0 auto;
  padding-bottom: 35px;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .doubt {
    @include vertical-paddings(67px);
    text-align: center;
  }

  .doubt__title {
    width: 415px;
    margin: 0 auto;
    padding-bottom: 30px;
    font-size: 30px;
  }

  .doubt__text {
    width: 430px;
    margin: 0 auto;
    padding-bottom: 75px;
  }
}

@media (min-width: $desktop-width) {
  .doubt {
    @include vertical-paddings(58px);
  }

  .doubt__title {
    padding-top: 15px;
  }

  .doubt__text {
    padding-bottom: 71px;
  }
}
